import React from "react";
import market from "../../config/market";
const CompareProductsOne = React.lazy(() => import("./CompareProductsOne"));

export default function () {
	switch (market.config.compareProductList) {
		case 1:
			return <CompareProductsOne addClass={"col-lg-3 col-md-4 col-6"} />;

		default:
			return <CompareProductsOne />;
	}
}
